.NavbarItems{
  background:-moz-radial-gradient(circle at 50% 50%, rgba(24, 0, 100, 1) 0%, rgba(3, 0, 23, 1) 99%);

  /* safari 5.1+,chrome 10+ */
  background:-webkit-radial-gradient(circle at 50% 50%, rgba(24, 0, 100, 1) 0%, rgba(3, 0, 23, 1) 99%);

  /* opera 11.10+ */
  background:-o-radial-gradient(circle at 50% 50%, rgba(24, 0, 100, 1) 0%, rgba(3, 0, 23, 1) 99%);

  /* ie 10+ */
  background:-ms-radial-gradient(circle at 50% 50%, rgba(24, 0, 100, 1) 0%, rgba(3, 0, 23, 1) 99%);

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 50% 50%, rgba(24, 0, 100, 1) 0%, rgba(3, 0, 23, 1) 99%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.navbar-logo{
  color: #fff;
  justify-self: start;
  margin-left: -15px;
  cursor: pointer;
  text-decoration: none;
  font-size: 30px;
  font-weight: bold;
}

.fa-solid fa-cubes{
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu{
  display : grid;
  grid-template-columns: repeat(5,auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content:right;
  margin-right: 3rem;
}

.nav-links{
  font-weight: bold;
  text-decoration: none;
  color: white;
  padding: 0.5rem 1rem;
}

.nav-links:hover{
  background-color: #6d76f7;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.fa-times{
  color: #fff;
}

.fa-bars{
  color:white;
}

.menu-icon{
  display:none;
}

.nav-links-mobile{
  display: none;
}

@media screen and (max-width: 960px){
  .NavbarItems{
    position: relative;
  }

  .nav-menu{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active{
    background: #1a1a3b;
    left: 0;
    opacity: 1;
    transition: all 0.5sec ease;
    z-index: 1;
  }

  .nav-links{
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover{
    background-color: #7577fa;
    border-radius: 0;
  }

  .navbar-logo{
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
}
